import React, { Component } from "react"
import { Card, CardBody, CardHeader } from "reactstrap"
import styled, { keyframes } from "styled-components"

const buttonClick = keyframes`
  0% {background: #F0F0F0}
  50% {background: #9999}
  100% {background: #F0F0F0}
`

const WrapLayout = styled.div`
  & .card-header {
    margin-bottom: 2rem;
    background: #d9d9d9;
    & h4 {
      color: #000;
      margin-bottom: 0;
    }
  }
  & .col-6,
  .col-12 {
    padding: 0;
  }
  & .input-css,
  .result-convert {
    padding: 10px;
    width: 100%;
    height: 300px;
    border: 2px solid #000;
    border-radius: 4px;
    color: #000;
  }
  & .col-12 {
    margin-bottom: 16px;
  }
  & .result-error {
    background: lightcoral;
  }
  & .btn-copy {
    min-height: 50px;
    margin-right: 4px;
    border-radius: 10px;
    box-shadow: 0 7px 14px 0 rgb(65 69 88 / 10%), 0 3px 6px 0 rgb(0 0 0 / 7%);
  }
  & .btn-animation {
    animation: ${buttonClick} 0.2s ease-in-out;
  }
  & .check-format {
    min-width: 20px;
    min-height: 20px;
  }
  & .button-group {
    display: flex;
    justify-content: end;
    align-items: center;
    color: #000;
    & label {
      font-size: 18px;
      font-weight: 600;
      margin: 0px 10px;
    }
  }
`
class MP_Css_2React_Style extends Component {
  constructor(props) {
    super(props)
    this.state = {
      cssStyle: `    margin: 0;font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";font-size: 0.88rem;
      font-weight: 400;
      line-height: 1.5;
      color: #495057;
      text-align: left;
      background-color: #fff;`,
      resultStyle: "",
      errorChange: false,
      isFormatResult: true,
      animationCopy: true,
    }
    this.resultRef = React.createRef()
  }
  componentDidMount() {
    this.renderChangeStyleReact()
  }
  renderChangeStyleReact = () => {
    let value = this.state.cssStyle
    const { isFormatResult } = this.state
    value = value.trim()
    const arrCss = value != "" ? value?.split(";") : []
    const error = arrCss?.find(item => {
      item = item.trim()
      const fieldName = item.split(":")
      const regex = /\-?^[a-zA-Z0-9_-]*$/
      if (fieldName.length === 1) {
        return true
      }
      return !regex.test(fieldName[0])
    })
    const arrLine = value != "" ? value?.split("\n") : []
    if (error) {
      const indexLineError = arrLine.findIndex(
        item => item.indexOf(error.replace(/(\r\n|\n|\r)/gm, "")) >= 0
      )
      let positionError = 0
      if (indexLineError >= 0) {
        positionError = arrLine[indexLineError]?.indexOf(";")
        positionError =
          positionError < 0 ? arrLine[indexLineError].length : positionError
      }
      this.setState({
        errorChange: true,
        resultStyle: `Error: undefined:${indexLineError + 1}:${
          positionError + 1
        }: property missing ':'`,
      })
    } else {
      const arrReactStyle = arrCss?.map(item => {
        const fieldName = item.split(":")
        if (fieldName.length < 2) return ""
        if (fieldName[0].indexOf("-") >= 0) {
          const fieldNameDetail = fieldName[0].split("-")
          fieldNameDetail[1] =
            fieldNameDetail[1].charAt(0).toUpperCase() +
            fieldNameDetail[1].slice(1)
          fieldName[0] = fieldNameDetail.join("")
        }
        fieldName[0] = fieldName[0].trim()
        fieldName[1] = fieldName[1].trim()
        return isFormatResult
          ? `   ${JSON.stringify(fieldName[0])}:${JSON.stringify(fieldName[1])}`
          : `${JSON.stringify(fieldName[0])}:${JSON.stringify(fieldName[1])}`
      })
      const result = isFormatResult
        ? `{\n${arrReactStyle?.join(",\n")}}`
        : `{${arrReactStyle?.join(",").replace(/(\r\n|\n|\r)/gm, "")}}`
      this.setState({
        errorChange: false,
        resultStyle: arrReactStyle.length > 0 ? result : "",
      })
    }
  }
  copyToClipboard = () => {
    var textField = document.createElement("textarea")
    textField.innerText = this.state.resultStyle
    document.body.appendChild(textField)
    textField.select()
    document.execCommand("copy")
    textField.remove()
  }
  handleOnChange = e => {
    this.setState({ cssStyle: e.target.value }, () => {
      this.renderChangeStyleReact()
    })
  }

  render() {
    return (
      <WrapLayout>
        <Card>
          <CardHeader>
            <h4>Css2 React Style Tool</h4>
          </CardHeader>
          <CardBody>
            <div className="container-fluid">
              <div className="row">
                <div className="col-12">
                  <div className="button-group text-right">
                    <button
                      className={`${
                        this.state.animationCopy ? "btn-animation" : ""
                      } btn-copy`}
                      onClick={e => {
                        this.setState({ animationCopy: true }, () => {
                          this.resultRef.current.select()
                        })
                        this.copyToClipboard()
                      }}
                      onAnimationEnd={() =>
                        this.setState({ animationCopy: false })
                      }
                    >
                      Copy to Clipboard
                    </button>
                    <label>Format</label>
                    <input
                      className="check-format"
                      onChange={e => {
                        this.setState(
                          {
                            isFormatResult: !this.state.isFormatResult,
                          },
                          () => {
                            this.renderChangeStyleReact()
                          }
                        )
                      }}
                      defaultChecked={this.state.isFormatResult}
                      type="checkbox"
                    />
                  </div>
                </div>
                <div className="col-6">
                  <textarea
                    className="input-css"
                    value={this.state.cssStyle}
                    onChange={e => {
                      this.handleOnChange(e)
                    }}
                    rows={10}
                  />
                </div>
                <div className="col-6">
                  <textarea
                    ref={this.resultRef}
                    className={`result-convert ${
                      this.state.errorChange ? "result-error" : ""
                    }`}
                    onChange={e => {}}
                    value={this.state.resultStyle}
                  />
                </div>
              </div>
            </div>
          </CardBody>
        </Card>
      </WrapLayout>
    )
  }
}
export default MP_Css_2React_Style
